<template lang="pug">
  
  .radio-stations
      .radio-stations__item(v-for='(station, index) in stations' @click='selectStation(index)')
        .radio-stations__info
          svg.radio-stations__icon
            use(:xlink:href='"#" + station.prefix')
          .radio-stations__song-info
            h1.radio-stations__title {{station.title}}
            p.radio-stations__song {{station.song}}

        button(
          :class="{'ol-music__play_playing': selectedIndex == index}"
          
        ).ol-music__play

          img(v-if='!player.paused && selectedIndex == index' src='@/assets/img/icons/pause.svg')
          img(v-else-if='player.paused && selectedIndex == index' src='@/assets/img/icons/play.svg')
          img(v-else src='@/assets/img/icons/play.svg')
    
      p.ol-music__footer Radio Record API #[span www.radiorecord.ru]

</template>

<script>


  export default {
    props: {
      stations: Array,
      selectedIndex: Number,
      player: Object
    },
    methods: {
      selectStation(i) {
        this.$store.commit('SELECT_STATION', i)
        this.$store.commit('RADIO_PLAY_CONTROL')
      },
    }
  }

</script>
