<template lang="pug">
  
  .ol-music

    .ol-radio-logo
      .ol-radio-logo__circle
        img(src='@/assets/img/icons/antenna.svg')
      .ol-radio-logo__text
        h1.ol-radio-logo__title OVERLIMIT
        h1.ol-radio-logo__subtitle Radio
    
    .current-song
      .current-song__player(@click='songDesc = true')
        button(@click.stop='play').ol-music__play.ol-music__play_playing
          img(v-if='!player.paused' src='@/assets/img/icons/pause.svg')
          img(v-else src='@/assets/img/icons/play.svg')

        .current-song__song-info(v-if='selectedStation')
          h1.current-song__artist {{ selectedStation.artist }}
          p.current-song__name {{ selectedStation.song }}

      VueSlider(
        v-model='player.volume' 
        :drag-on-click="true" 
        :tooltip="'none'").ol-music__slider.ol-music__slider_current


    Stations(:stations='stations'  :player='player' :selectedIndex='selectedIndex')

    transition(name='fade')
      MusicDesc(v-if='songDesc' :currentSong='selectedStation' @hideDesc='hideDesc')

</template>

<script>

  import {mapState} from 'vuex'

  import VueSlider from 'vue-slider-component'
  

  import Stations from '../components/Music/Stations'
  import MusicDesc from '../components/Music/MusicDesc'

  export default {
    components: { Stations, MusicDesc, VueSlider },
    data() {
      return { songDesc: false }
    },
    computed: {
      ...mapState({
        stations: state => state.music.stations,
        selectedIndex: state => state.music.selectedStationIndex,
        player: state => state.music.player,
        radio: state => state.music.radio
      }),
      selectedStation() {
        return this.stations[this.selectedIndex]
      }
    },
    methods: {
      play() {
        this.player.paused = !this.player.paused
        this.$store.commit('RADIO_PLAY_CONTROL')
      },
      hideDesc(data) {
        this.songDesc = data
      }
    },
    created() { 
      this.$store.commit('REFRESH_SYSTEM_STYLE', false);
      this.$store.dispatch('GET_ALL_DATA')
    },
    destroyed() {
      this.$store.dispatch('DESTROY_INTERVAL')
    }
  }
</script>

<style lang="sass"> @import '../assets/sass/phone/apps/music/main' </style>