<template lang="pug">
  
  .music-description
    .music-description__img-wrapper
      button.music-description__hide(@click='hideDesc')
        svg
          use(xlink:href='#arrow')

      img(:src='getSongPicture').music-description__img
    .music-description__desc
      p.music-description__title {{currentSong.title}}
      h1.music-description__artist {{currentSong.artist}}
      p.music-description__song {{currentSong.song}}

      button(@click='play').music-description__play
        img(v-if='!player.paused' src='@/assets/img/icons/pause.svg')
        img(v-else src='@/assets/img/icons/play.svg')
      
      VueSlider(v-model='player.volume' :drag-on-click="true" :tooltip="'none'").ol-music__slider.ol-music__slider_desc
      

</template>

<script>
  
  import {mapState} from 'vuex'
  import VueSlider from 'vue-slider-component'

  export default {
    props: { currentSong: Object },
    components: { VueSlider },
    computed: {
      ...mapState({
        images: state => state.music.images,
        player: state => state.music.player
      }),
      getSongPicture() {
        let findImage = this.images.find(({song}) => song == this.currentSong.song)

        return findImage
          ? findImage.image600
          : this.currentSong ? this.currentSong.icon_png : null
      }
    },

    methods: {
      play() {
        this.player.paused = !this.player.paused
        this.$store.commit('RADIO_PLAY_CONTROL')
      },
      hideDesc() {
        this.$emit('hideDesc', false)
      }
    }
  }

</script>

<style lang="sass"> @import '../../assets/sass/phone/apps/music/desc' </style>